<template>
    <div>
        <top-nav title="产品评价列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>这里是产品评价列表</p>
            <p>1)注意：列表中的审核通过会再商品评论中显示并自动赠送积分，审核拒绝则不会显示并且不赠送积分</p>
        </div>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">

                    <el-form-item label="订单ID" label-width="100">
                        <el-input v-model="searchForm.order_id"></el-input>
                    </el-form-item>
                    <el-form-item label="商品名称" label-width="100">
                        <el-input v-model="searchForm.title"></el-input>
                    </el-form-item>
                    <el-form-item label="用户昵称" label-width="100">
                        <el-input v-model="searchForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="是否显示">
                        <el-select v-model="searchForm.is_show" placeholder="请选择">
                            <el-option :key="-999" label="请选择" :value=-999></el-option>
                            <el-option :key="1" label="是" :value=1></el-option>
                            <el-option :key="0" label="否" :value=0></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="评价时间">
                        <el-date-picker v-model="searchForm.bg_add_time" type="datetime"
                                        placeholder="选择日期时间"></el-date-picker>
                        至
                        <el-date-picker v-model="searchForm.end_add_time" type="datetime"
                                        placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getCommentList(1)" size="mini" type="primary">搜索
                        </el-button>
                        <a href="javascript:void(0);" @click="searchForm.title = '',searchForm.is_show = -999,
            searchForm.bg_add_time = '',searchForm.end_add_time = '',getCommentList()">取消搜索</a>
                    </div>
                </el-form>

                <el-table :data="datas.list" style="width: 100%" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <!--<el-table-column label="评论ID" prop="comment_id" width="70"></el-table-column>-->

                    <el-table-column label="用户" width="155">
                        <template slot-scope="scope">
                            <p>用户ID:{{scope.row.user_id}} |
                                <router-link target="_blank" :to="'/customer/view/'+scope.row.user_id">
                                    <el-button type="text">跟踪</el-button>
                                </router-link></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="订单ID"  width="155">
                        <template slot-scope="scope">
                            订单ID:{{scope.row.order_id}} |
                            <router-link target="_blank" :to="'/order/product/view/'+scope.row.order_id">
                                <el-button type="text">跟踪</el-button>
                            </router-link>
                        </template>
                    </el-table-column>

                    <el-table-column label="标题">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.title}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="评论晒图" width="180">
                        <template slot-scope="scope">
                            <div class="block" style="display:inline-block;float: left;"
                                 v-for="(item,index) in scope.row.img">
                                <img v-if="index <= 2" v-bind:src="item" width="50"/> &nbsp;
                            </div>
                            <div v-if="scope.row.img.length > 3">...</div>
                            <el-button type="text" href="javascript:void(0);" @click="photos=scope.row.img,showP=true">查看</el-button>
                        </template>
                    </el-table-column>

                    <el-table-column label="评分" width="150">
                        <template slot-scope="scope">
                            <el-rate :value="scope.row.goods_rank" :v-model="scope.row.goods_rank" disabled show-score text-color="#ff9900" score-template="{value}"></el-rate>
                        </template>
                    </el-table-column>
                    <el-table-column label="评论内容" width="200">
                        <template slot-scope="scope">
                            <el-input type="textarea"  resize="none" :rows="2" placeholder="请输入内容" v-model="scope.row.content">
                            </el-input>
                        </template>

                    </el-table-column>
                    <el-table-column label="商家回复" width="200">
                        <template slot-scope="scope">
                            <el-input type="textarea"  resize="none" :rows="2" placeholder="请输入内容" v-model="scope.row.reply">
                            </el-input>
                        </template>
                    </el-table-column>


                    <el-table-column label="操作" width="280">
                        <template slot-scope="scope">
                            <el-button type="text" @click="commentId=scope.row.comment_id,form.reply=scope.row.reply,dialog=true">回复</el-button>&nbsp;
                            <el-button v-if="scope.row.is_show==0" type="text" @click="hideComment(scope)">审核拒绝</el-button>
                            <el-button v-if="scope.row.is_show==0" type="text" @click="showComment(scope)">审核通过</el-button>
                            <el-popover style="margin: 0 10px;" placement="top" width="160"
                                        v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button :loading="loading" size="mini" type="text"
                                               @click="scope.row.is_delete = false">取消
                                    </el-button>
                                    <el-button :loading="loading" type="primary" size="mini"
                                               @click="deleteComment(scope)">确定
                                    </el-button>
                                </div>
                                <el-button type="text" slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-tool">

                    </div>
                    <div class="page-mian">
                        <el-pagination @current-change="getCommentList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog title="回复评论" v-loading="subLoading" :visible.sync="dialog" width="50%" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small">
                <el-form-item label="回复评论" label-width="80px">
                    <el-input type="textarea" :rows="5" placeholder="请输入内容" v-model="form.reply"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="reply(true)">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="showP" width="500px" :modal-append-to-body="false">
            <el-carousel :interval="5000" arrow="always" >
                <el-carousel-item v-for="item in photos">
                    <img v-bind:src="item"  width="500"/>
                </el-carousel-item>
            </el-carousel>
        </el-dialog>
    </div>

</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'commentIndex',
        components: {TopNav},
        data() {
            return {
                showP: false,
                dialogTableVisible: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //修改 添加 form

                //搜索 form
                searchForm: {
                    title: '',  // 手机号码
                    is_show: -999,
                    end_add_time: '',
                    bg_add_time: '',
                },
                commentId: 0,
                commentIds: [],
                cats: [],
                dialog: false,
                subLoading: false,
                photos: [],
                form: {
                    reply: '',
                },
            }
        },
        created() {
            this.getCommentList();
        },
        methods: {
            /**
             * 获取产品评价列表
             * @param page {number} 页数
             */
            getCommentList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.goods.comment/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    title: this.searchForm.title,
                    username: this.searchForm.username,
                    order_id: this.searchForm.order_id,
                    goods_id: this.searchForm.goods_id,
                    is_show: this.searchForm.is_show,
                    end_add_time: this.searchForm.end_add_time,
                    bg_add_time: this.searchForm.bg_add_time,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            /**
             * 处理全选事件
             */
            handleSelectionChange(e) {
                this.commentIds = [];
                e.forEach((item, index) => {
                    this.commentIds.push(item.comment_id);
                });

            },

            showCommentByIds() {
                this.loading = true;
                api.post("/shop/admin.goods.comment/showComment", {
                    appToken: this.$store.getters.appToken,
                    commentIds: JSON.stringify(this.commentIds),
                }).then(res => {
                    this.getCommentList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            hideCommentByIds() {
                this.loading = true;
                api.post("/shop/admin.goods.comment/hideComment", {
                    appToken: this.$store.getters.appToken,
                    commentIds: JSON.stringify(this.commentIds),
                }).then(res => {
                    this.getCommentList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            showComment(scope) {
                this.loading = true;
                api.post("/shop/admin.goods.comment/showComment", {
                    appToken: this.$store.getters.appToken,
                    comment_id: scope.row.comment_id,
                }).then(res => {
                    for (let i in this.datas.list) {
                        if (i == scope.$index) {
                            this.datas.list[i].is_show = 1;
                        }
                    }
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            hideComment(scope) {
                this.loading = true;
                api.post("/shop/admin.goods.comment/hideComment", {
                    appToken: this.$store.getters.appToken,
                    comment_id: scope.row.comment_id,
                }).then(res => {
                    for (let i in this.datas.list) {
                        if (i == scope.$index) {
                            this.datas.list[i].is_show = 0;
                        }
                    }
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });

            },


            deleteComment(scope) {
                this.loading = true;
                let comments = [];
                api.post("/shop/admin.goods.comment/delComment", {
                    appToken: this.$store.getters.appToken,
                    comment_id: scope.row.comment_id,
                }).then(res => {
                    for (let i in this.datas.list) {
                        if (i != scope.$index) {
                            comments.push(this.datas.list[i])
                        }
                    }
                    this.datas.list = comments;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },




            reply() {
                this.subLoading = true;
                api.post("/shop/admin.goods.comment/reply", {
                    appToken: this.$store.getters.appToken,
                    comment_id: this.commentId,
                    reply: this.form.reply,
                }).then(res => {
                    this.getCommentList();
                    this.loading = false;
                    this.subLoading = false;
                    this.dialog = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                }).catch((error) => {
                    this.loading = false;
                    this.subLoading = false;

                });
            },
        }
    }
</script>
